* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  min-height: 100vh;
  background-color: #fafafa;
  flex-direction: column;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

img {
  max-width: 100%;
  display: block;
}

.button-box {
  min-height: 100%;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  gap: 40px;
  display: flex;
}

button[data-start] {
  width: 200px;
  height: 80px;
  color: #fff;
  cursor: pointer;
  background-color: #8abe8a;
  border: none;
  border-radius: 10px;
  padding: 8px 16px;
  font-size: 24px;
}

button[data-stop] {
  width: 200px;
  height: 80px;
  color: #fff;
  cursor: pointer;
  background-color: #c88;
  border: none;
  border-radius: 10px;
  padding: 8px 16px;
  font-size: 24px;
}

